.public {
  background: linear-gradient(to right, rgba(68, 157, 68, 0.6) 10px, #fff 10px);
}

.arrow {
  position: relative;
}

.arrow:after {
  content: " ";
  position: absolute;
  border: solid transparent;
  height: 0;
  width: 0;
  pointer-events: none;
  border-width: 30px;
}

.arrow-xs:after {
  border-width: 20px;
  margin-left: -20px;
}

.arrow-bottom-color-secondary:after {
  border-top-color: #325077;
}

.arrow-bottom:after {
  top: 100%;
  left: 50%;
  margin-left: -30px;
}

.arrow-bottom-xs:after {
  margin-left: -20px;
}

.episode-box {
  position: relative;
  padding: 20px;
  height: 200px;
  overflow: hidden;
  background-position: center, center;
  background-size: cover;
  color: #fff;
}

.episode-box:hover .episode-desc {
  display: block;
}

.episode-box--big {
  height: auto;
}

.episode-desc {
  display: none;
}

.episode-label {
  position: absolute;
  left: 0;
  bottom: 0;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.less-episodes {
  height: 200px;
  overflow: hidden;
}

.partner-indicators {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.7);
}

.videowrapper {
  float: none;
  clear: both;
  width: 100%;
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 25px;
  height: 0;
}

.videowrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video-play {
  position: absolute;
  bottom: 50%;
  left: 50%;
  right: 50%;
  margin-left: -56px;
}

.opacity-none {
  opacity: 0;
  z-index: -9999;
}

.position-relative {
  position: relative;
}

